body {
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 600;
}

.container {
  flex-direction: column;
  display: flex;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 2px solid #0a66c2;
  border-radius: 100px;
  padding: 10px;
}

.search-input input {
  height: 40px;
  border: none;
  outline: none;
  font-size: 16px;
}

.search-input button {
  height: 100%;
  color: #fff;
  background: #0a66c2;
  border: none;
  border-radius: 16px;
  transition: background-color .167s cubic-bezier(.4, 0, .2, 1), box-shadow .167s cubic-bezier(.4, 0, .2, 1), color .167s cubic-bezier(.4, 0, .2, 1);
}

.search-input button:hover, .search-input button:focus {
  color: #fff;
  background-color: #16437e;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.photo-card {
  width: calc(25% - 30px);
  background-color: #f0f6ff;
}

.photo-card img {
  height: 320px;
  width: 100%;
  object-fit: contain;
}

.info {
  justify-content: center;
  column-gap: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.info-item span {
  font-weight: 200;
}

.load-more {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  max-width: 480px;
  min-height: 40px;
  min-width: 0;
  text-align: center;
  touch-action: manipulation;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  background-color: #0a66c2;
  border: 0;
  border-radius: 100px;
  justify-content: center;
  align-self: center;
  align-items: center;
  margin-top: 20px;
  padding: 0 20px;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  transition: background-color .167s cubic-bezier(.4, 0, .2, 1), box-shadow .167s cubic-bezier(.4, 0, .2, 1), color .167s cubic-bezier(.4, 0, .2, 1);
  display: none;
  overflow: hidden;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #16437e;
}

.load-more:active {
  color: rgba(255, 255, 255, .7);
  background: #09223b;
}

.loader {
  height: 12px;
  width: 80%;
  border: 1px solid #fff;
  border-radius: 10px;
  display: none;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  height: 100%;
  width: 0;
  background-color: #0a66c2;
  animation: prog 6s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes prog {
  to {
    width: 100%;
  }
}

